// export default Filters;

import { useState } from "react";
import { RadioGroup, RadioGroupItem } from "./ui/radio-group";
import { Label } from "./ui/label";
import { ChevronDown, ChevronUp } from "lucide-react";

const filterData = [
  {
    filterType: "Location",
    options: [
      "Shikarpur",
      "Sukkur",
      "Hyderabad",
      "Karachi",
      "Islamabad",
      "Rawalpindi",
      "Lahore",
      "Multan",
      "Peshawar",
      "Bahawalpur",
      "Sargodha",
    ],
  },
  {
    filterType: "Job Type",
    options: ["Full Time", "Part Time", "Contract", "Internship"],
  },
  {
    filterType: "Experience",
    options: ["0-1 years", "1-2 years", "2-3 years", "3+ years"],
  },
  {
    filterType: "Salary",
    options: ["10-20k", "20-30k", "30-40k", "40-50k", "50-60k", "60-80k"],
  },
];

const Filters = ({ onFilterChange }) => {
  const [openFilters, setOpenFilters] = useState({});
  const [selectedFilters, setSelectedFilters] = useState({});

  const toggleFilter = (filterType) => {
    setOpenFilters((prev) => ({
      ...prev,
      [filterType]: !prev[filterType],
    }));
  };

  const handleSelection = (filterType, value) => {
    setSelectedFilters((prev) => {
      const updatedFilters = { ...prev, [filterType]: value };
      onFilterChange(updatedFilters); // Pass selected filters to `Jobs`
      return updatedFilters;
    });
  };

  return (
    <div className="p-4">
      <p className="text-xl font-bold mb-3 text-gray-800">Filters</p>
      <hr className="mb-4" />

      {filterData.map((data, i) => (
        <div key={i} className="mb-4">
          <div
            className="flex justify-between items-center cursor-pointer"
            onClick={() => toggleFilter(data.filterType)}
          >
            <p className="font-semibold text-lg">{data.filterType}</p>
            {openFilters[data.filterType] ? (
              <ChevronUp className="text-gray-600" />
            ) : (
              <ChevronDown className="text-gray-600" />
            )}
          </div>

          {openFilters[data.filterType] && (
            <div className="mt-2 space-y-2">
              <RadioGroup
                value={selectedFilters[data.filterType] || ""}
                onValueChange={(value) =>
                  handleSelection(data.filterType, value)
                }
              >
                {data.options.map((item, index) => (
                  <div
                    key={`${data.filterType}-${index}`}
                    className="flex items-center space-x-2"
                  >
                    <RadioGroupItem value={item} />
                    <Label>{item}</Label>
                  </div>
                ))}
              </RadioGroup>
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default Filters;
